:root {
  --font-museo-sans-cyrl: 'MuseoSansCyrl';
  --header-height-top-block-desk: 4rem;
  --header-height-bottom-block-desk: 10rem;
  --header-height-desk: calc(var(--header-height-top-block-desk) + var(--header-height-bottom-block-desk));
  --header-height-top-block-mobile: 3rem;
  --header-height-bottom-block-mobile: 6rem;
  --header-height-mobile: calc(var(--header-height-top-block-mobile) + var(--header-height-bottom-block-mobile));

  --page-content-padding-fl: 8rem;
  --page-content-padding-bg: 5rem;
  --page-content-padding-small: 2rem;
  --page-content-padding-xxs: 1.6rem;

  //colors
  --main-color: #FA4907;
  --main-color-hover: #FF7846;
  --main-text-color: #2B2B2B;
  --main-grey-color: #A6A6A6;
  --grey-text: #555759;
  --border-color: #F5F5F5;
  --border-color-2: #DBDBDB;
  --box-shadow-input: 0 4px 4px rgba(51, 51, 51, 0.04), 0 4px 16px rgba(51, 51, 51, 0.08);

  // z-index
  --z-index-header: 3;
  --z-index-roaming-top-block: 2;

  //font size
  // !     
  //     _consts
  --fs-h1: 3.8rem;
  --fs-h2: 2.8rem;
  --fs-h3: 2rem;

  --fs-t1: 1.8rem;
  --fs-t2: 1.6rem;
  --fs-t3: 1.4rem;

  @include for-bg-down {
    --fs-h1: 3.2rem;
    --fs-h2: 2.4rem;
    --fs-h3: 1.8rem;

    --fs-t1: 1.6rem;
    --fs-t2: 1.5rem;
    --fs-t3: 1.4rem;
  }
  @include for-sm-down {
    --fs-h1: 2.6rem;
    --fs-h2: 2.2rem;
    --fs-h3: 1.7rem;

    --fs-t1: 1.5rem;
    --fs-t2: 1.4rem;
    --fs-t3: 1.3rem;
  }
  @include for-xs-down {
    --fs-h1: 2.2rem;
    --fs-h2: 1.8rem;
    --fs-h3: 1.6rem;

    --fs-t1: 1.4rem;
    --fs-t2: 1.3rem;
    --fs-t3: 1.2rem;
  }

  // numbered-list
  //    ,  -  
  --nl-left-padding: 11rem;
  --nl-vertical-padding: 2rem;
  --nl-right-padding: 2.5rem;

  @include for-bg-down {
    --nl-left-padding: 8rem;
  }
  @include for-sm-down {
    --nl-left-padding: 6rem;
    --nl-vertical-padding: 1.5rem;
    --nl-right-padding: 2rem;
  }
  @include for-xs-down {
    --nl-left-padding: 4rem;
    --nl-vertical-padding: 1.2rem;
    --nl-right-padding: 1.2rem;
  }
}
