body {
  color: var(--main-text-color);
  
  &.mod-no-scroll {
    overflow: hidden;
    height: 100vh;
  }
}

a {
  cursor: pointer;
  transition: color 250ms;

  &:hover {
    color: var(--main-color);

    svg {
      fill: var(--main-color);
    }
  }
}

svg {
  transition: fill 250ms;
}

button {
  &:hover {
    svg {
      fill: var(--main-color);
    }
  }
}

::selection {
 @extend %select-orange-background;
}

.gl__page-content {
  padding-left: var(--page-content-padding-fl);
  padding-right: var(--page-content-padding-fl);
  max-width: 1560px;
  margin-left: auto;
  margin-right: auto;

  @include for-bg-down {
    padding-left: var(--page-content-padding-bg);
    padding-right: var(--page-content-padding-bg);
  }

  @include for-sm-down {
    padding-left: var(--page-content-padding-small);
    padding-right: var(--page-content-padding-small);
  }

  @include for-xxs-down {
    padding-left: var(--page-content-padding-xxs);
    padding-right: var(--page-content-padding-xxs);
  }
}

.gl__layout-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

.gl__layout-content {
  flex: 1 1 100%;
  margin-top: var(--header-height-desk);

  @include for-bg-down {
    margin-top: var(--header-height-mobile);
  }
}

.gl__title-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4rem;

  .gl__title {
    margin-bottom: 0;
  }

  @include for-sm-down {
    margin-bottom: 2.6rem;
  }
}

.gl__title {
  font-size: 3.8rem;
  line-height: 5rem;
  letter-spacing: 0.015em;
  color: var(--main-text-color);
  margin-bottom: 5rem;
  font-weight: normal;

  @include for-sm-down {
    font-size: 2.6rem;
    line-height: 3.4rem;
    margin-bottom: 2.6rem;
  }

  &.mod-white {
    color: #fff;
  }
}

.gl__white-container {
  box-shadow: 0 0 12px 4px #0000000f;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
}

.gl__drop-menu-btn {
  &.mod-open {
    .gl__drop-menu-icon {
      transform: rotate(-180deg);
    }
  }
}

.gl__drop-menu-icon {
  transform: rotate(0);
  transition: transform 250ms;
}

.gl__drop-menu-container {
  max-height: 0;
  overflow: hidden;
  transition: max-height 400ms;

  &.mod-open {
    //max-height: ??; //   js
    pointer-events: auto;
  }

  &:not(.mod-open) {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.gl__drop-menu {
  padding-bottom: 2rem;
}

.gl__menu-tab-content {
  &:not(.mod-show) {
    display: none;
  }
}
