.faq {
  padding: 4rem 0 8rem;

  @include for-sm-down {
    padding: 2rem 0 4rem;
  }
}

.faq__item {
  &:not(.mod-no-border) {
    border-bottom: 2px solid var(--border-color);
  }
}

.faq__item-btn-wrapper {
  display: flex;
  font-size: 1.6rem;
  line-height: 2.6rem;
  letter-spacing: 0.02em;
  padding: 2rem 0;
  font-weight: 700;
  text-align: start;
  width: 100%;

  &:hover {
    color: var(--main-color);
  }
}

.faq__item-content-wrapper {
  margin-left: 4.2rem;
  padding-bottom: 2rem;
}

.faq__item-btn-icon {
  fill: var(--main-text-color);
  margin-right: 1.8rem;
  height: 2.4rem;
  flex: 0 0 2.4rem;
}

.faq__item-content-text {
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 2.6rem;
  letter-spacing: 0.02em;

  & + & {
    margin-top: 1rem;
  }
}

.faq__link-show-all {
  font-size: 1.6rem;
  line-height: 2rem;
  letter-spacing: 0.01em;
  padding: 2rem 0;
  font-weight: 700;
  display: flex;
  align-items: center;
  margin-top: 2.2rem;
}

.faq__link-show-all-icon {
  fill: var(--main-text-color);
  height: 2.4rem;
  padding-top: .4rem;
}

.faq__flexs-list {
  margin-left: 10.4rem;

  @include for-sm-down {
    margin-left: 3rem;
  }

  @include for-xs-down {
    margin-left: 0;
  }
}

.faq__flexs-item {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 2rem;
  letter-spacing: 0.02em;

  & + & {
    border-top: 1px solid var(--border-color);
  }
}

.faq__flexs-item-caption {
  font-weight: 300;
}

.faq__flexs-item-value {
  font-weight: 700;
}
