.gl-slider {
  position: relative;
}

.gl-slider__container {
  padding: 2rem;
  margin: -2rem;

  @include for-hg-down {
    padding: 1.2rem;
    margin: -1.2rem;
  }

  @include for-bg-down {
    margin-right: calc(var(--page-content-padding-bg) * -1);
  }

  @include for-sm-down {
    margin-right: calc(var(--page-content-padding-small) * -1);
  }

  @include for-xxs-down {
    margin-right: calc(var(--page-content-padding-xxs) * -1);
  }
}

.gl-slider__wrapper {
  display: flex;

  @include for-bg-down {
    margin-right: var(--page-content-padding-bg);
  }

  @include for-sm-down {
    margin-right: var(--page-content-padding-small);
  }

  @include for-xxs-down {
    margin-right: var(--page-content-padding-xxs);
  }
}

.gl-slider__slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 5rem;
  height: 5rem;
  background-color: #fff;
  box-shadow: 0 0.266004px 10px rgba(153, 152, 168, 0.2);
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;

  &.mod-prev {
    left: -6.6rem;
  }

  &.mod-next {
    right: -6.6rem;
  }
}

.gl-slider__slider-btn-icon {
  height: 2.4rem;
}

.gl-slider__pagination-container {
  display: flex;
  justify-content: center;
}

.gl-slider__pagination-wrapper {
  &.swiper-pagination {
    bottom: -5.4rem;
    z-index: 1;
  }

  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    border-radius: 100%;
    background-color: #DBDBDB;
    margin-right: 1rem;

    &:last-child {
      margin-right: 0;
    }
  }

  .swiper-pagination-bullet-active {
    background-color: var(--main-color);
  }
}

